import { AngularFireAuth } from '@angular/fire/auth';
import { UserService } from './../../SERVICES/user.service';
import { Component, AfterViewInit, OnChanges } from '@angular/core';
import { Router } from '@angular/router';
import { AngularFirestore } from '@angular/fire/firestore';


@Component({
  selector: 'app-assomyevents',
  templateUrl: './assomyevents.page.html',
  styleUrls: ['./assomyevents.page.scss'],
})
export class AssomyeventsPage implements OnChanges {
  Collection
  items
  userId
  constructor(private Afs: AngularFirestore, private user: UserService, private router: Router, private afAuth: AngularFireAuth) {
   
  }
  goTo(postID: string) {

    console.log( postID)
    this.router.navigate(['/detailevents/' + postID])
  }

  ngOnChanges() { 
 
    
  }
  ionViewWillEnter(){
    this.afAuth.authState.subscribe(auth => {
      this.userId = auth.uid;
      console.log("userID:   " + this.userId)
    });
    console.log("oldmethodId    " + this.userId)
    console.log("newmethodId    " + this.user.getUID())
    this.Collection = this.Afs.collection('/publicevents', ref => ref.where('host', '==', this.user.getUID()))
  this.items = this.Collection.valueChanges();
  }
  

}
